header{
    width: 100%;
    background: var(--technic-orange);
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.headerWrapper{
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo{
    display: flex;
    align-items: center;
}

.logoImg{
    background: url(../../images/Logo.svg);
    width: 22px;
    height: 22px;
    background-repeat: no-repeat;
    background-size: 105%;
}

.logo span{
    font-family: "Mulish", sans-serif;
    font-weight: 800;
    font-style: italic;
    color: #fff;
    font-size: 22px;
    text-transform: uppercase;
    margin-left: 4px
}

.navigationBar{
    display: flex;
    align-items: center;
}

nav{
    padding: 8px 16px;
    background: #fff;
    border-radius: 8px;
    border: 1px solid var(--menu-outline);
}

ul{
    display: flex;
    list-style-type: none
}

li{
    font-family: "Inter", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 13px;
    color: var(--menu-inactive);
    padding: 4px 16px;
    user-select: none;
    cursor: pointer;
}

li:first-child{
    padding-left: 0;
}

li:nth-child(2){
    border-right: 1px solid var(--menu-outline);
    border-left: 1px solid var(--menu-outline);
}

li:last-child{
    padding-right: 0;
}

li:hover{
    color: var(--menu-active);
}

.callButton{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background: #fff;
    border-radius: 8px;
    border: 1px solid var(--menu-outline);
    margin-left: 16px;
}