.carBrandsLine{
    width: 100%;
    height: 208px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 1px solid var(--outline);
}

.carBrand{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-right: 1px solid var(--outline);
}

.carBrand:hover{
    background: var(--technic-orange);
}