.aboutUs{
    width: 100%;
    display: flex;
}

.aboutUsTextWrapper{
    background: var(--technic-grey);
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.aboutUsTextWrapper > *{
    padding-left: 12%;
}

.aboutUsTitle > h1{
    font-family: "Random Grotesque Spacious Black", sans-serif;
    font-style: italic;
    font-weight: 900;
    line-height: 48px;
    text-transform: uppercase;
    color: #000;
    margin-bottom: 124px;
}

.aboutUsInfo{
    display: flex;
    max-width: 864px;
}

.aboutUsInfo:not(:last-child){
    margin-bottom: 40px;
}

.aboutUsNumber{
    margin-right: 42px;
}

.aboutUsPoint{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-bottom: 24px;
    border-bottom: 1px solid var(--underline);
}
.aboutUsInfoTitle{
    width: 40%;
}
.aboutUsInfoTitle > h2{
    font-family: Onest, sans-serif;
    font-size: 32px;
    color: #222;
    font-weight: 600;
}

p{
    font-family: Onest, sans-serif;
    font-size: 14px;
    color: #000;
    font-weight: 400;
}

.aboutUsInfoText{
    width: 60%;
}

.aboutUsCircles{
    padding: 12px;
}
.aboutUsCircle{
    width: 375px;
    height: 375px;
    animation: rotating 3s infinite linear normal;
    user-select: none;
    pointer-events: none;
}

.aboutUsCircle:last-child{
    animation: rotating 3s infinite linear reverse;
}

@keyframes rotating {
    0%{
    }
    100%{
        rotate: 360deg;
    }
}

.aboutUsCircle > img{
    width: 100%;
    height: 100%;
}
