@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Mulish:ital,wght@0,200..1000;1,200..1000&family=Onest:wght@100..900&display=swap');
@import url(https://db.onlinewebfonts.com/c/050e8298df214bf7250a6ae986d02376?family=Random+Grotesque+Spacious+Black);

:root{
    --background-color: #191A1B;
    --menu-outline: #EBEBEB;
    --menu-inactive: #5E5E5E;
    --menu-active: #222222;
    --technic-orange: #FD4D16;
    --outline: #525252;
    --technic-grey: #F7F9FD;
    --underline: #D3D3D3
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background: var(--background-color);
    overflow-x: hidden;
}