.mainInfo{
    width: 100%;
    padding: 32px;
}

.mainInfoPicts{
    width: 100%;
    display: flex;
    justify-content: center;
    position: relative;
}

.mainInfoPict{
    height: 687px;
    transition: width .5s ease-out;
}

.mainInfoPictOne{
    height: 687px;
    transition: width .5s ease-out;
}

.mainInfoPictOne > img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 16px;
    filter: brightness(.9);
}

.mainInfoPict > img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 16px;
    filter: brightness(.9);
}

.mainInfoPicts > div:nth-child(2){
    padding: 0 8px;
}

.mainInfoTextBlock{
    width: 80vw;
    position: absolute;
    bottom: 100px;
}

.mainInfoTextWrapper{
    display: flex;
    align-items: center;
}

.mainInfoButton{
    width: 169px;
    height: 40px;
    background: var(--technic-orange);
    border-radius: 8px
}

h1, h2, button{
    font-family: Onest, sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    color: #fff;
}

h1{
    font-size:  48px;
    line-height: 48px;
}

h2{
    font-size: 24px;
    line-height: 24px;
}

.mainInfoMainText{
    margin-top: 40px;
}

.headText{
    font-family: "Random Grotesque Spacious Black", sans-serif;
    font-style: italic;
    font-weight: 900;
    line-height: 48px;
    text-transform: uppercase;
}

button{
    font-size: 16px;
    padding-block: 0;
    padding-inline: 0;
    border-width: 0;
    cursor: pointer;
}

.mainInfoButton:hover{
    background: #fff;
    color: #000;
}

